.media {
  display: flex;
}
.media + .media {
    margin-top: (@spacer * 3);
}
.media-body {
  flex: 1;

  > *:last-child {
      margin-bottom: 0;
  }
}
.media-middle {
  align-self: center;
}
.media-bottom {
  align-self: flex-end;
}


//
// Images/elements as the media anchor
//

.media-object {
  display: block;
}


//
// Alignment
//

.media-right {
  padding-left: @media-object-padding-horizontal;
}

.media-left {
  padding-right: @media-object-padding-horizontal;
}


//
// Headings
//

.media-heading {
  margin-top: 0;
  margin-bottom: 0;
}


//
// Media list variation
//

.media-list {
  padding-left: 0;
  list-style: none;
}
